export default {
  bind(el) {
    el.oninput = function (e) {
      if (!e.isTrusted) {
        return;
      }
      
      const mask = "(__) ___-__-__";

      var phoneNumber = this.value.replace(/[^0-9]/g, '');
      
      this.value = formatPhoneNumber(phoneNumber);

      el.dispatchEvent(new Event('input'));

      var lastDigitIndex = lastDigit(this.value);

      el.setSelectionRange(lastDigitIndex, lastDigitIndex);
      
      function formatPhoneNumber(phoneNumber){
        var value = mask;
        for(let number of phoneNumber){
          value = value.replace("_", number);
        }
        return value;
      }

      function lastDigit(string) {
        for (let i = string.length - 1; i >= 0; i--) {
          if (/\d/.test(string[i])) {
            return i+1;
          }
        }
        return 1;
      }
    };
  },
};