import Vue from 'vue'
import Vuex from 'vuex'

import { initializeApp } from "firebase/app";

import {
  updateDoc,
  getDoc,
  getDocs,
  doc,
  collection,
  getFirestore,
  setDoc,
  query, where,
} from 'firebase/firestore';

import {
  getAuth,
  signOut,
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  GoogleAuthProvider,
  signInWithPopup
} from 'firebase/auth';

const APP = initializeApp({
  apiKey: "AIzaSyD9oZKAndhy3OoYhs330TOB-9gHe4QUMlU",
  authDomain: "regulation-of-repairs.firebaseapp.com",
  projectId: "regulation-of-repairs",
  storageBucket: "regulation-of-repairs.appspot.com",
  messagingSenderId: "800212177449",
  appId: "1:800212177449:web:29a3b00109d00651424238"
});

const DB = getFirestore(APP);
const AUTH = getAuth(APP);

const provider = new GoogleAuthProvider();
provider.addScope('https://www.googleapis.com/auth/contacts.readonly');
provider.setCustomParameters({
  'login_hint': 'user@example.com'
});

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoggedIn: localStorage.getItem('uid'),
    loginErrorMessage: '',
    createErrorMessage: '',
    userInfo: '',
  },

  getters: {
    isLoggedIn(state) {
      return state.isLoggedIn;
    },
    getLoginErrorMessage(state){
      return state.loginErrorMessage;
    },
    getCreateErrorMessage(state){
      return state.createErrorMessage;
    },
    getUserInfo(state){
      return state.userInfo;
    }
  },

  mutations: {
    clearErrorsData(state){
      state.loginErrorMessage= '';
      state.createErrorMessage= '';
    }
  },

  actions: {
    getAllUsers(){
      return getDocs(collection(DB, 'Accounts'));
    },
    getAllDrivers(){
      return getDocs(query(collection(DB, "Accounts"), where("role", "==", 'driver')));
    },
    getAllCars(){
      return getDocs(collection(DB, 'Cars'));
    },
    getAllCarsModels(){
      return getDoc(doc(DB, 'CarModels', 'models'));
    },
    saveNewModelsCollection(context, newData){
      return setDoc(doc(DB, 'CarModels', 'models'), newData);
    },
    getAllUserRoles(){
      return getDocs(collection(DB, 'UserRoles'));
    },
    updateUserRole(context, newData){
      return updateDoc(doc(DB, 'Accounts', newData.uid), {role: newData.role})
    },
    addAccount(context, userData) {
      return setDoc(doc(DB, 'Accounts', userData.uid), userData);
    },
    
    async setAccountInfo(context, uid){
      return getDoc(doc(DB, 'Accounts', uid)).then((result)=>{
        context.state.userInfo = result.data();
      });
    },
    login(context, userCred) {
      if (userCred.email.length && userCred.password.length) {
        signInWithEmailAndPassword(AUTH, userCred.email, userCred.password)
          .then((result) => {
            localStorage.setItem('uid', result.user.uid);
            context.state.isLoggedIn = true;
          })
          .catch((error) => {
            console.log(error.code)
            switch (error.code) {
              case 'auth/invalid-credential':
                context.state.loginErrorMessage = 'Неправильна пошта або пароль'
                break
              default:
                context.state.loginErrorMessage = 'Не вдалося увійти'
            }
          })
      }
    },
    createAccountWithGoogle(context) {
      signInWithPopup(AUTH, provider)
        .then((result) => {
          // This gives you a Google Access Token. You can use it to access the Google API.
          //const credential = GoogleAuthProvider.credentialFromResult(result);
          //console.log(credential.accessToken);
          const userFio = result.user.displayName.split(" ");
          var userData = {
            uid: result.user.uid,
            firstname: userFio[0],
            lastname: userFio.length < 2 ? '' : userFio[1],
            surname: userFio.length < 3 ? '' : userFio[2],
            email: result.user.email,
            phoneNumber: result.user.phoneNumber,
            role: ''
          }
          localStorage.setItem('uid', userData.uid);
          context.dispatch("addAccount", userData);
          context.state.isLoggedIn = true;
        }).catch((error) => {
          console.log(error.code)
          switch (error.code) {
            case 'auth/invalid-credential':
              context.state.createErrorMessage = 'Неправильна пошта або пароль'
              break
            default:
              context.state.createErrorMessage = 'Гугл реєстрація не вдалася'
          }
        });
    },
    createAccount(context, authInfo){
      if (authInfo.email.length && authInfo.password.length) {
        createUserWithEmailAndPassword(AUTH, authInfo.email, authInfo.password)
        .then((userCred) => {
          var userData = {
            uid: userCred.user.uid,
            firstname: authInfo.firstname,
            lastname: authInfo.lastname,
            surname: authInfo.surname,
            email: authInfo.email,
            phoneNumber: authInfo.phone.replace(/[^0-9]/g, '') ? authInfo.phoneRegion+authInfo.phone.replace(/[^0-9]/g, '') : null,
            role: ''
          }
          localStorage.setItem('uid', userData.uid);
          context.dispatch("addAccount", userData);
          context.state.isLoggedIn = true;
        })
        .catch((error) => {
          switch (error.code) {
            case 'auth/invalid-credential':
              context.state.createErrorMessage = 'Неправильна пошта або пароль'
              break
            case 'auth/email-already-in-use':
              context.state.createErrorMessage = 'Пошта вже зайнята'
              break
            default:
              context.state.createErrorMessage = 'Реєстрація не вдалася'
          }
        })
      }
    },
    logout(context) {
      signOut(AUTH).then(() => {
        context.state.isLoggedIn = false;
        context.state.userInfo = null;
        localStorage.removeItem('fio');
        localStorage.removeItem('uid');
      }).catch((error) => {
        console.log(error);
      });
    }
    
  },

  modules: {
  }
})
