<template>
    <div class="menu-container">
        <span class="company-name">Belogvi</span>
        <div class="user-name-icon-container">
            <span class="user-fio">{{fio}}</span>
            <div class="user-profile-button">
                <img v-if="!isMouseOver" src="../assets/images/user-icon-img.svg" @click="toggleUserIcons()" alt="" >
                <img v-if="isMouseOver" src="../assets/images/user-icon-img-active.svg" @click="toggleUserIcons()" alt="" >
            </div>
            <div v-if="isProfileMenuVisible" class="menu-choice-container">
                <ul  class="profile-menu">
                    <li>Профіль</li>
                    <li class="end-session" @click="logout()">Вихід з акаунту</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
name: 'MenuBar',

props: {
  
},

data() {
    return {
        isProfileMenuVisible: false,
        isMouseOver: false,
        userInfo: '',
        fio: localStorage.getItem('fio') ? localStorage.getItem('fio') : ''
    };
  },

  methods: {
    toggleUserIcons(){
        this.isMouseOver = !this.isMouseOver;
        this.isProfileMenuVisible = !this.isProfileMenuVisible;
    },
    setUserFio(){
        if(!localStorage.getItem('fio')){
            var result = this.userInfo.firstname;
            if(this.userInfo.lastname)
                result+=" "+this.userInfo.lastname;
            localStorage.setItem("fio", result);
            this.fio = result;
        }
        this.fio = localStorage.getItem('fio');
        
    },
    logout(){
        this.$store.dispatch('logout');
    }
  },

  computed: {
    
  },

  mounted() { 
    if(!localStorage.getItem('userInfo')){
        this.$store.dispatch('setAccountInfo', localStorage.getItem('uid')).then(()=>{
        this.userInfo = this.$store.getters['getUserInfo'];
        this.setUserFio();
    });
    }
    
  },
  }
</script>