<template>
    <div class="login-create-window">
      <div class="create-account-box">
        <h3 class="title">{{createAccountWindow ? "Вікно реєстрації" : "Вікно авторизації"}}</h3>
        <div class="form">
          <div class="fio-box" v-if="createAccountWindow">
            <div class="first-name-box">
              <input 
                type="text" 
                id="first-name" 
                placeholder="Ім'я *" 
                v-model="userCred.firstname"
                :class="{'invalid-register-inputs-true': !isValidName(userCred.firstname) && (userCred.firstname || emptyInputData)}"
              >
              <label 
                for="first-name" 
                class="invalid-register-inputs" 
                v-if="!isValidName(userCred.firstname) && (userCred.firstname || emptyInputData)"
              >
                {{ emptyInputData && !userCred.firstname ? "Заповніть поле" : "Некорректно"}}
              </label>
            </div>
            <div class="last-name-box">
              <input 
                type="text" 
                id="last-name" 
                placeholder="Прізвище" 
                v-model="userCred.lastname"
                :class="{'invalid-register-inputs-true': !isValidName(userCred.lastname) && userCred.lastname}"
              >
              <label 
                for="last-name" 
                class="invalid-register-inputs" 
                v-if="!isValidName(userCred.lastname) && userCred.lastname"
              >
              Некорректно
              </label>
            </div>
            <div class="surname-box">
              <input 
                type="text" 
                id="surname" 
                placeholder="По-батькові" 
                v-model="userCred.surname"
                :class="{'invalid-register-inputs-true': !isValidName(userCred.surname) && userCred.surname}"
              >
              <label 
                for="surname" 
                class="invalid-register-inputs" 
                v-if="!isValidName(userCred.surname) && userCred.surname"
              >
              Некорректно
              </label>
            </div>
          </div>
          <div class="phone-number-container" v-if="createAccountWindow">
            <div class="ukraine-number-code">
              <span>{{userCred.phoneRegion}}</span>
            </div>
            <input
            type="tel" 
            v-model="userCred.phone"
            :class="{'invalid-register-inputs-true': userCred.phone.replace(/[_\-)(\s]/g, '').length && userCred.phone.replace(/[_\-)(\s]/g, '').length < 9}"
            id="phone"
            placeholder="(__) ___-__-__"
            autocomplete="tel"
            v-phone
          />
          </div>
          
          <label 
            for="phone" 
            class="invalid-register-inputs" 
            v-if="userCred.phone.replace(/[_\-)(\s]/g, '').length && userCred.phone.replace(/[_\-)(\s]/g, '').length < 9 && createAccountWindow"
          >
            Номер неповний
          </label>

          <input 
            type="email" 
            id="email" 
            :placeholder="createAccountWindow ? 'Email *' : 'Email'"
            autocomplete="email"
            v-model="userCred.email" 
            :class="{ 'invalid-register-inputs-true': !isValidEmail && (userCred.email || emptyInputData) }"
          >
          <label 
            for="email" 
            class="invalid-register-inputs" 
            v-if="!isValidEmail && (userCred.email || emptyInputData)"
          >
            {{ emptyInputData && !userCred.email ? "Введіть пошту" : "Неправильна пошта"}}
          </label>

          <input 
            type="password" 
            id="password" 
            :placeholder="createAccountWindow ? 'Пароль *' : 'Пароль'" 
            v-model="userCred.password" 
            :class="{'invalid-register-inputs-true': passwordErrorMessage && (this.userCred.password || emptyInputData)}"
          >
          <input 
            type="password" 
            placeholder="Підтвердіть пароль *" 
            v-model="secondPassword" 
            :class="{'invalid-register-inputs-true': passwordErrorMessage && (this.userCred.password || emptyInputData)}"
            v-if="createAccountWindow"
          >
          <label 
            for="password" 
            class="invalid-register-inputs" 
            v-if="passwordErrorMessage && (this.userCred.password || emptyInputData)"
          >
            {{passwordErrorMessage}}
          </label>

          
          

        </div>
        <span class="error-alert" v-if="registerAndLoginErrorMessage">{{registerAndLoginErrorMessage}}</span>
        <div class="login-create-actions">
          <button @click="createAccountWindow ? createAccount() : login()" class="login-create-account-btn">{{createAccountWindow ? "Зареєструватися" : "Увійти"}}</button>
          <button @click="toggleStartWindow()" class="login-create-btn">{{ createAccountWindow ? "До авторизації" : "До реєстрації"}}</button>
        </div>
        <template>
          <div class="registration-description">
          <span class="subtitle">або за допомогою</span>
        </div>
        <button class="social-register">
            <div class="google-registration-box" @click="createAccountWithGoogle()">
              <img src="../assets/images/google-color-icon.png" alt="">
              <span>продовжити з Google</span> 
              <div></div>
            </div>
        </button>
        </template>
      </div>
    </div>
  </template>
  
  <script>
  import PhoneDirective from '../directives/PhoneDirective';
  export default {
    name: 'AuthorizationAndRegistration',
    props: {
    },
    

    directives: {
      phone: PhoneDirective,
    },

    data() {
        return {
            createAccountWindow: false,
            emptyInputData: false,
            secondPassword: '',
            userCred: {
                phoneRegion: '+380',
                email: '',
                password: '',
                firstname: '',
                lastname: '',
                surname: '',
                phone: '',
        },
    };
  },

  methods: {
    login() {
      (this.isValidEmail && this.userCred.password) 
        ? this.$store.dispatch('login', this.userCred)
        : this.emptyInputData = true;
    },
    toggleStartWindow() {
      this.emptyInputData = false;
      this.createAccountWindow = !this.createAccountWindow;
      if(this.loginErrorMessage || this.createErrorMessage)
        this.clearErrorsData();
    },
    createAccountWithGoogle() {
      this.$store.dispatch('createAccountWithGoogle');
    },
    createAccount(){
      (this.isValidEmail && ((this.userCred.phone.replace(/[_\-)(\s]/g, '').length == 9 && this.userCred.phone.replace(/[_\-)(\s]/g, '').length) || !this.userCred.phone.replace(/[_\-)(\s]/g, '').length) 
      && (this.isValidName(this.userCred.firstname) && (this.isValidName(this.userCred.lastname) || !this.userCred.lastname) && (this.isValidName(this.userCred.surname) || !this.userCred.surname)) 
      && this.isPasswordsEqual) 
        ? this.$store.dispatch('createAccount', this.userCred)
        : this.emptyInputData = true;
    },
    isValidName(name) {
      return /^[a-zA-Zа-яА-ЯёЁґҐіІїЇєЄ' -]+$/.test(name);
    },
    clearErrorsData() {
      this.$store.commit('clearErrorsData');
    }
  },

  computed: {
    isValidPhone(){
      
      return /^((\+?3)?8)?((0\(\d{2}\)?)|(\(0\d{2}\))|(0\d{2}))\d{7}$/.test(this.userCred.phoneNumber.replace(/[\s-]/g, ''));
    },
    isLoggedIn() {
      return this.$store.getters['isLoggedIn'];
    },
    isValidEmail() {
      return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.userCred.email);
    },
    isPasswordsEqual() {
      return ((this.userCred.password === this.secondPassword) && (this.userCred.password))
    },
    loginErrorMessage() {
      return this.$store.getters.getLoginErrorMessage;
    },
    createErrorMessage(){
      return this.$store.getters.getCreateErrorMessage;
    },
    passwordErrorMessage(){
      if(this.createAccountWindow){
        if(this.userCred.password && this.userCred.password.length < 6 && this.secondPassword && this.userCred.password!=this.secondPassword)
          return "Пароль занадто короткий та не співпадає";
        if(this.userCred.password && this.userCred.password.length < 6)
          return "Пароль занадто короткий";
        if(this.userCred.password && !this.secondPassword){
          return "Введіть повторний пароль";
        }else if(!this.isPasswordsEqual && this.secondPassword){
          return "Поролі не співпадають";
        }else if(this.emptyInputData && !this.userCred.password){
          return "Введіть пароль"
        }
      }
      if(!this.userCred.password && this.emptyInputData)
        return "Введіть пароль"
      return '';
    },
    registerAndLoginErrorMessage(){
      if(this.createErrorMessage && this.createAccountWindow){
        return this.createErrorMessage;
      }else if(this.loginErrorMessage && !this.createAccountWindow){
        return this.loginErrorMessage;
      }else{
        return false;
      }
    }
  },

  created(){
  },

  mounted() { 

  },
  }
  </script>
  